.home__banner{
    background-image:url('../assetts/image7.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -500px;
    height: 600px;
    color: rgb(0, 0, 0);
    display: flex;
    justify-content: center;

}

.home__banner h1 {
    font-size:4rem;
    text-shadow: 5px 6px rgb(255, 255, 255);
}

.home__subBanner {
    background-color: rgb(3, 2, 2);
    color: rgb(238, 238, 238);
    display: flex;
    justify-content: center;
    padding: 10px;
}