.header {
    min-height: 80px;
    display: flex;
    top: 0;
    padding: 0%;
    z-index: 100;
  }
  .header__menu {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 0%;
}

.header .header__nav a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: 2rem;
    font-weight: bold;
  }
